import React from 'react';
import Card from '@components/Card';
import {
  PostLayout,
  PostFileContent,
  PostMarkdownContent,
} from '@components/Pages/Post/Styles';
import Utterances from '@components/Utterances';
import { UTTERANCES_THEME } from '../../../constants/utterances';
import { ContentBox } from './Styles';

interface Props {
  rootMarkdownHtml: string;
  utterances?: string;
}

function AboutPresenter({ rootMarkdownHtml, utterances }: Props) {
  return (
    <ContentBox>
      {rootMarkdownHtml && (
        <Card title="소개">
          <PostLayout>
            <PostFileContent>
              <PostMarkdownContent>
                <div
                  style={{
                    padding: '1.25rem',
                  }}
                  dangerouslySetInnerHTML={{
                    __html: rootMarkdownHtml,
                  }}
                />
              </PostMarkdownContent>
            </PostFileContent>
          </PostLayout>
        </Card>
      )}

      {utterances && (
        <Card title="방명록">
          <Utterances repo={utterances} theme={UTTERANCES_THEME.GITHUB_LIGHT} />
        </Card>
      )}
    </ContentBox>
  );
}

export default AboutPresenter;
