import React from 'react';
import { PageProps } from 'gatsby';
import { useSetTitle } from '@hooks/useSetTitle';
import { defineCustomElements as deckDeckGoElement } from '@deckdeckgo/highlight-code/dist/loader';
import AboutPresenter from './Presenter';

import 'katex/dist/katex.min.css';

deckDeckGoElement();

interface QueryProps {
  file: {
    children: Array<{
      html: string;
    }> | null;
  } | null;
}

function AboutContainer({
  data: { file },
  pageContext: { repoName, utterances },
}: PageProps<QueryProps, PageContext>) {
  useSetTitle(repoName);

  const rootMarkdownHtml = ((): string => {
    if (!file) {
      return '';
    }

    const { children } = file;

    if (!children) {
      return '';
    }

    return children[0].html;
  })();

  return (
    <AboutPresenter
      rootMarkdownHtml={rootMarkdownHtml}
      utterances={utterances}
    />
  );
}

export default AboutContainer;
